* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Manrope, sans-serif;
}

html {
  font-size: 62.5%;
}

.con-calendar {
  width: 100%;
  height: 100vh;
  color: #eee;
  background-color: #12121f;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calendar {
  width: 250px;
  height: 300px;
  background-color: #f8f8f8;
  border-radius: 20px;
  padding: 27px 16.5px 20px;
  overflow: hidden;
  box-shadow: 0 10px 60px rgba(0, 0, 0, .1);
}

.month-year {
  display: flex;
}

.month {
  width: 55%;
  margin-right: auto;
}

.year {
  width: 33%;
}

.month, .year {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 13px;
  padding: 0;
  display: flex;
}

.month i, .year i {
  cursor: pointer;
  color: #4440f6;
  font-size: 17px;
}

.month h1, .year h2 {
  letter-spacing: -.408px;
  text-transform: uppercase;
  color: #000;
  margin: auto;
  padding: 0 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.38;
}

.month p {
  letter-spacing: .38px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.79;
}

.weekdays {
  width: 100%;
  align-items: center;
  margin-bottom: 14px;
  display: flex;
}

.weekdays div {
  width: 18px;
  height: 18px;
  letter-spacing: -.078px;
  width: 14.2857%;
  text-align: center;
  color: rgba(60, 60, 67, .3);
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

.days {
  width: 100%;
  flex-wrap: wrap;
  display: flex;
}

.days div {
  letter-spacing: .38px;
  width: 14.2857%;
  height: 31px;
  color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 0 2px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.79;
  transition: background-color .2s;
  display: flex;
}

.days div:hover:not(.today) {
  height: 31px;
  width: 31px;
  cursor: pointer;
  background-color: #4440f6;
  border: 1px solid #777;
  border-radius: 50%;
  margin: auto;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.prev-date, .next-date {
  opacity: .5;
}

.today {
  background-color: #4440f6;
}

